/* SLIDER */

.slider_wrapper{
    position: relative;
    height: 70vh;
}

.slider_img{
    display: none;
}

.slider_txt{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.slider_txt p{
    font-family: 'Red Hat Display';
    font-weight: bold;
    font-size: 4.5rem;
    line-height: 133%;
    color: #333545;
    text-align: center;
    max-width: 350px;
}

.slider_bar{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin-bottom: 5vh;
}

.slider_bar .bar div {
    width: 0;
    height: 3px;
    background-color: #45B5C9;
}

.progress_bar_beginning{
    -webkit-animation: progress_bar_beginning 10s alternate infinite;
            animation: progress_bar_beginning 10s alternate infinite;
}

.progress_bar_end{
    -webkit-animation: progress_bar_end 10s alternate infinite;
            animation: progress_bar_end 10s alternate infinite;
}

@-webkit-keyframes progress_bar_beginning {
    from{
        width: 0%;
    }

    to{
        width: 100%;
    }
}

@keyframes progress_bar_beginning {
    from{
        width: 0%;
    }

    to{
        width: 100%;
    }
}

@-webkit-keyframes progress_bar_end {
    from{
        width: 100%;
    }

    to{
        width: 0%;
    }
}

@keyframes progress_bar_end {
    from{
        width: 100%;
    }

    to{
        width: 0%;
    }
}

.slider_bar .bar{
    width: 200px;
    height: 3px;
    background: #E1E8F2;
    border-radius: 5px;
}

.slider_bar p{
    font-family: 'Red Hat Display';
    font-weight: 900;
    color: #818498;
    font-size: 1.2rem;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    margin: 0 1rem;
}

@media all and (max-height: 500px) and (orientation: landscape){
    .slider_bar{
        margin-bottom: 2.5rem;
    }
}

@media all and (min-width: 500px){
    .slider_txt p{
        font-size: 5.4rem;
    }
}


@media all and (min-width: 1024px){
    .slider_img{
        position: relative;
        display: block;
        height: 100%;
    }
    .slider_img img{
